const auth = {
    computed: {
        roles() {
            return this.$store.getters['common/getRoles']
        }
    },
    methods: {
        /**
         * 校验角色权限
         * @param {String} id 菜单id
         * @param {String} role 角色权限：CRUDA
         */
        handleRole(id, role) {
            let nr = this.roles.find(oo => oo.id === id)
                if (!!nr && nr.role.includes(role)) {
                    return true
                } else {
                    this.$toast.fail('你没有修改数据的权限')
                    return false
                }
        }
    }
}

export default auth