<template>
    <layout title="项目管理" @addEvent="addEvent">
        <ul class="supplier-container">
            <li v-for="it in list" :key="it.id">
                <div>{{it.name}}</div>
                <span>负责人：{{it.liableName}}</span>
                <span>签订日期：{{it.signTime | dateFormat('yyyy-MM-dd hh:mm:ss')}}</span>
                <span class="t">地址：{{it.address}}</span>
                <i class="opt-btn">
                    <i @click="btnEvt('edit', it)"></i>
                    <i @click="btnEvt('delete', it)" class="del"></i>
                </i>
            </li>
            <li v-if="loading" class="more">
                <van-loading color="#1989fa" /> 数据加载中...
            </li>
            <li v-else class="more">
                {{hasMore ? '上滑加载更多数据' : '没有更多数据'}}
            </li>
        </ul>
    </layout>
</template>

<script>
    import { projectDeleteApi, projectListApi } from '../../apis/ProjectApi'
    import Auth from '../Auth'

    export default {
        mixins: [Auth],
        data() {
            return {
                list: [],
                paging: {
                    page: 1,
                    size: 10
                },
                total: 0,
                loading: true,
                hasMore: true
            }
        },
        async created() {
            this.getList()
        },
        methods: {
            async getList() {
                this.$toast.loading({
                    message: '数据加载中...',
                    forbidClick: true,
                    duration: 0
                })
                let result = await projectListApi(this.paging)
                this.$toast.clear()
                if (result.code === 200) {
                    this.list = this.list.concat(result.data.rows)
                    this.total = result.data.total
                    this.hasMore = this.total > (this.paging.page * this.paging.size) 
                }

                await this.$nextTick()
                this.loading = false
            },
            scorllEvt(evt) {
                let target = evt.target,
                    fullHeight = target.scrollHeight,
                    viewHeight = target.clientHeight,
                    disHeight = target.scrollTop,
                    isBottom = (fullHeight - viewHeight) <= (disHeight + 12)

                if (isBottom && !this.loading && this.hasMore) {
                    this.loading = true
                    this.paging.page += 1
                    this.getList()
                }
            },
            btnEvt(type, row) {
                switch (type) {
                    case 'edit':
                        if (!this.handleRole('PROJECT-01-01', 'U')) return
                        this.$store.commit('common/mutationEditData', row)
                        this.$router.push({path: '/project/edit', query: {id: row.id}})
                        break
                    case 'delete':
                        if (!this.handleRole('PROJECT-01-01', 'D')) return
                        this.$dialog.confirm({
                                title: '删除提示',
                                message: `确定要删除【${row.name}】这个项目的信息？`,
                            }).then(async () => {
                                let result = await projectDeleteApi(row.id)
                                if (result.code === 200) {
                                    let idx = this.list.findIndex(it => it.id === row.id)
                                    this.list.splice(idx, 1)
                                    this.$toast.success('删除项目信息成功')
                                } else {
                                    this.$toast.fail('删除项目信息成功')
                                }
                            }).catch(() => {})
                        break
                    default:
                        break
                }
            },
            addEvent() {
                if (!this.handleRole('PROJECT-01-01', 'C')) return
                this.$router.push('/project/edit')
            }
        }
    }
</script>